
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'

export default {
components: {
Flexbox,
FlexboxItem
},
props: [
'mul',
'mulGator',
'proTime',
'mulPic',
'like',
'mulContent',
'comment',
'transpond'
],
methods: {
collect (con) {
this.$vux.toast.show({
type: 'text',
position: 'center',
text: con
})
}
}
}



